* {
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
}
button {
  font-size: 28px;
  background-color: #19b1ab;
  border-radius: 6px;
  border: none;
  color: #1b3a51;
  padding: 10px;
  text-transform: uppercase;
  width: 200px;
  cursor: pointer;
  box-shadow: 0 5px 15px black;
}
button:hover {
  font-size: 30px;
}
#placeholder {
  height: 100px;
  width: 100px;
  padding: 50px;
  margin: 50px auto;
  border: 1px solid gray;
  border-radius: 10px;  
  font-size:80px;
  box-shadow: 0 8px 15px;
  background: #b7d25e;
  color: #1b3a51;
}

body {
  background: #fae4ab;
}
div{
  width: 300px;
  background-color: red;
  margin: auto;
}
h1 {
  color: #1b3a51;
}